import React from 'react';
import { useConfirmationApi, useRouter } from 'shared/hooks';

import { useAuth } from 'modules/auth/hooks/useAuth';
import { Helmet } from 'react-helmet';

function ActivatePage() {
	const { isAuthentificated } = useAuth();
	const { history, location } = useRouter();
	//Custom hook to handle confirming links from email
	useConfirmationApi(handleRedirect, location.pathname.includes('transfer'));

	function handleRedirect() {
		if (isAuthentificated) {
			history.push('/switch-business');
			return;
		}
		history.push('/signin');
	}

	return (
		<div className={'h-full w-full p-8'}>
			<Helmet>
				<title>KENNEKT | Activation</title>
			</Helmet>

			<span>
				Your request is being handling. You will be redirected to{' '}
				<span>{isAuthentificated ? 'switch business' : 'sign in'}</span> page after process.
			</span>
		</div>
	);
}

export default ActivatePage;
